


































































































































import {Vue,Component, Watch} from 'vue-property-decorator';
import ActionBlock from '@/components/search/actionBlock.vue';
import Service from '../../https/hostProductType'
import Add from './add.vue';
import EcsService from '../../https/instance/create';
import {trans} from '../../utils/transIndex';       
import svgIcon from '@/components/svgIcon/index.vue';
@Component({
    components:{
        ActionBlock,
        Add,
        svgIcon
    }
})
export default class ProductType extends Vue{
    private search_dom:any={
        az_id:{placeholder:'',list:[]},
        host_product_id:{placeholder:'id'},
        cpu_name:{placeholder:'CPU'},
        gpu_card_name:{placeholder:''},
        network_card_type:{placeholder:''},
    }
    private visible:boolean=false
    private list:any=[{id:'1'}];
    private search_data:any={}
    private operLabel = 'add';
    private operInfo:any={};
    private authList:any=[]
    private page_info:any={
        current:1,
        size:20,
        total:0
    }
    created(){
        this.get_az_list();
        this.getHostTypes();
        this.authList = this.$store.state.auth_info[this.$route.name];
    }
    @Watch('visible')
    private watch_visible(nv){
        if(!nv){
            this.getHostTypes()
        }
    }
    private FnSearch(data:any={}){
        this.page_info.current=1;
        this.search_data = {...data};
        this.getHostTypes()
    }
    private async getHostTypes(){
        const {az_id,host_product_id,cpu_name,gpu_card_name,network_card_type}=this.search_data
        let res:any = await Service.get_host_type({
            az_id,
            host_product_id,
            cpu_name,
            gpu_card_name,
            network_card_type,
            page_index:this.page_info.current,
            page_size:this.page_info.size,
        })
        if(res.code==="Success"){
            this.list = res.data.host_product_list;
            this.page_info.total = res.data.page_info.count
        }
    }
    private async get_az_list(){
    let res:any=await EcsService.get_region_az_list({})
        if(res.code==="Success"){
            res.data.forEach(item=>{
                item.region_list.forEach(inn=>{
                this.search_dom.az_id.list=[...this.search_dom.az_id.list,...trans(inn.az_list,'az_name','az_id','label','type')]
                })
            })
        }
    }
    private handleSizeChange(size){
        this.page_info.size = size
        this.getHostTypes()
    }
    private handleCurrentChange(cur){
        this.page_info.current = cur
        this.getHostTypes()
    }
    private add(val,obj){
        this.operLabel = val;
        this.operInfo=obj;
        this.visible=true
    }
    private async down(){
        const {
            az_id,
            host_product_id,
            cpu_name,
            gpu_card_name,
            network_card_type,
        }=this.search_data
        let obj = {
            az_id,
            host_product_id,
            cpu_name,
            gpu_card_name,
            network_card_type,
        }
        let str=""
        for (let i in obj){
        if(obj[i]){
            str =str+`${i}=${obj[i]}&`
        }
        }
        let query = str==="" ? "" : `?${str.slice(0,str.length-1)}`
        console.log('query',query);
        
        window.location.href=`/ecs_business/v1/host/host_product_list_download/${query}`
    }
}
