



















































































import { ElForm } from 'element-ui/types/form';
import { Component, Prop, PropSync, Emit, Watch, Vue } from 'vue-property-decorator';
import Service from '../../https/hostProductType';

@Component
export default class AddPod extends Vue {
  $message;
  @PropSync('visible', {type: Boolean}) syncVisible!: boolean;
  @Prop({default: ''}) title!: string;
  @Prop({default: () => ({
    host_product_id:''
  })}) row!: any;
  private formData:any = {
    id:this.row.host_product_id ? this.row.host_product_id : '',
    name: this.row.host_product_id ?  this.row.name : '',
    cpu: this.row.host_product_id ? this.row.cpu_name : '',
    cpuNum:this.row.host_product_id ? Number(this.row.cpu_size) : 1,
    nuclear:this.row.host_product_id ? Number(this.row.logic_cpu_size) : '',
    memory:this.row.host_product_id ? Number(this.row.ram_capacity) : '',
    memoryNum:this.row.host_product_id ? Number(this.row.ram_size) : 1,
    gpu:this.row.host_product_id ? this.row.gpu_name : '',
    gpuNum:this.row.host_product_id ? Number(this.row.gpu_size) : 1,
    gpuMemory:this.row.host_product_id ? Number(this.row.gpu_capacity) : '',
    disk:[{
      data_disk_size:1,
      data_disk_feature:'',
      data_disk_capacity:1,
      data_disk_unit:'GB',
      data_disk_mode:'',
    }],
    system:{
      system_disk_size:1,
      system_disk_feature:'',
      system_disk_capacity:1,
      system_disk_unit:'GB',
      system_disk_mode:'',
    },
    unit:this.row.host_product_id ? this.row.disk_unit : 'GB',
    diskNum:this.row.host_product_id ? Number(this.row.disk_size) : 1,
    net:this.row.host_product_id ? this.row.network_card_type : '',
    netNum:this.row.host_product_id ? Number(this.row.network_card_size) : 1,
  };
  created() {
    if(this.row.host_product_id){
      this.formData.disk = this.row.data_disk_info
      for(let i in this.formData.system){
        this.formData.system[i] = this.row[i]
      }
    }
    
  }
  private validNuclear = (rule, value, callback)=>{        
    if(!value){      
      return callback(new Error(''))
    }
    else if((value % 2)!==0){      
      return callback(new Error('=CPU*2*'))
    }else{
      callback()
    }
  }
  private validSystem = (rule, value, callback)=>{    
    console.log('validSystem',value);
    if(Object.values(value).some(item=>!item)){      
      return callback(new Error(''))
    }else{
      callback()
    }
  }
  private validDisk = (rule, value, callback)=>{  
    console.log('validDisk',value);  
    if(value.some(item=>!item.data_disk_size || !item.data_disk_feature || !item.data_disk_capacity || !item.data_disk_mode)){      
      return callback(new Error(''))
    }else{
      callback()
    }
  }
  private rules = {
    name: [{ required: true, trigger: 'blur', message: '' }],
    cpu: [{ required: true, trigger: 'blur', message: 'CPU' }],
    nuclear: [{ required: true, validator:this.validNuclear}],
    memory: [{ required: true, trigger: 'blur', message: '' }],
    disk: [{ required: true, validator:this.validDisk}],
    system: [{ required: true, validator:this.validSystem}],
    net: [{ required: true, trigger: 'blur', message: '' }],
  }
  private add(){
    this.formData.disk.push({
      data_disk_size:1,
      data_disk_feature:'',
      data_disk_capacity:1,
      data_disk_unit:'GB',
      data_disk_mode:'',
    })
  }
  private del(index){
    this.formData.disk = this.formData.disk.filter((item,i)=>i!==index)
  }
  private FnConfirm(){
    let form = this.$refs.form as any;
    const {name,cpu,cpuNum,nuclear,memory,
          memoryNum,gpu,gpuNum,gpuMemory,
          net,netNum,disk,system
    }=this.formData;
    let req:any={
      name,
      cpu_name:cpu,
      cpu_size:cpuNum,
      logic_cpu_size:nuclear,
      ram_size:memoryNum,
      ram_capacity:memory,
      gpu_capacity:gpu ? gpuMemory : undefined,
      gpu_size:gpu ? gpuNum : undefined,
      gpu_card_name:gpu,
      network_card_type:net,
      network_card_size:netNum,
      ...system,
      data_disk_info:disk
    }
    form.validate(async valid=>{
      if(valid){
        let res:any;
        if(this.title===''){
          res = await Service.add({
            ...req,
          })
        }else{
          res = await Service.edit({
            host_product_id:this.formData.id,
            ...req,
          })
        }
        
        if(res.code==='Success'){
          this.syncVisible=false;
          this.$message.success(res.message)
        }
      }
    })
  }
}
